<template>
    
    <div class="ceguesa">
        <h2>CEGUESA</h2>
        
      <h3>VES EN COMPTE AL MAR</h3>

      <br>
      
        <video controls width="800">
        <source :src="require('@/assets/soloaudio.mp4')">
        </video>
        <div>
        <b-button variant="outline-primary">VEURE VIDEO</b-button>
        </div>

        <h3>DISFRUTA DE LES IMATGES DELS PEIXOS MÉS IMPRESIONANTS</h3>
        <div>
          <b-img src="../assets/1.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/2.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/3.jpg" fluid alt="Fluid image"></b-img>
        </div>
        <div>
          <b-img src="../assets/4.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/5.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/6.jpg" fluid alt="Fluid image"></b-img>
        </div>
        <div>
          <b-img src="../assets/7.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/8.jpg" fluid alt="Fluid image"></b-img>
          <b-img src="../assets/9.jpg" fluid alt="Fluid image"></b-img>
        </div>

        <div>
    <h3 style="text-align: center;">PEIXOS ALS RIUS DE CATALUNYA</h3>
    <canvas id="fishChart" width="800" height="500"></canvas>
    <p style="text-align: center;">Passa el cursor sobre les barres per veure els peixos.</p>
  </div>


        
<br><br>
<footer aria-label="Footer">
  <div class="container-footer">
<p>PC-ando | &copy; Berta Güell i Sònia Soldevila | 2024-2025</p>
</div>
</footer>

      </div>
    
</template>
  
  <script>
 export default {
  data() {
    return {
      fishData: [], // Aquí se almacenarán los datos del JSON
      hoveredIndex: -1 // Índice de la barra sobre la que está el cursor
    };
  },
  methods: {
    // Método para dibujar el gráfico
    drawChart() {
      const canvas = document.getElementById("fishChart");
      const ctx = canvas.getContext("2d");

      const chartWidth = canvas.width;
      const chartHeight = canvas.height;
      const margin = 60;
      const barWidth = (chartWidth - margin * 2) / this.fishData.length;

      // Encontrar la población máxima
      const maxPopulation = Math.max(...this.fishData.map(f => f.population));

      // Dibujar barras
      this.fishData.forEach((fish, index) => {
        const barHeight = (fish.population / maxPopulation) * (chartHeight - margin * 2);
        const x = margin + index * barWidth;
        const y = chartHeight - margin - barHeight;

        // Si el índice está siendo "hovered" se resalta la barra
        if (this.hoveredIndex === index) {
          ctx.fillStyle = "rgba(224, 153, 67, 1)";
        } else {
          // Usamos un degradado para el color de las barras
          const gradient = ctx.createLinearGradient(0, y, 0, y + barHeight);
          gradient.addColorStop(0, "rgba(67, 146, 224, 1)");
          gradient.addColorStop(1, "rgba(67, 146, 224, 0.5)");
          ctx.fillStyle = gradient;
        }

        ctx.fillRect(x, y, barWidth - 10, barHeight);

        // Etiqueta del nombre del pez
        ctx.fillStyle = "#000";
        ctx.font = "14px Helvetica";
        ctx.textAlign = "center";
        ctx.fillText(fish.name, x + (barWidth - 10) / 2, chartHeight - margin + 20);

        // Si está "hovered", mostramos la población
        if (this.hoveredIndex === index) {
          ctx.fillText(`Quantitat: ${fish.population}`, x + (barWidth - 10) / 2, y - 10);
        }
      });

      // Dibujar los ejes
      ctx.beginPath();
      ctx.moveTo(margin, margin);
      ctx.lineTo(margin, chartHeight - margin);
      ctx.stroke();

      ctx.beginPath();
      ctx.moveTo(margin, chartHeight - margin);
      ctx.lineTo(chartWidth - margin, chartHeight - margin);
      ctx.stroke();
    },

    // Método para detectar el hover sobre las barras
    handleMouseMove(event) {
      const canvas = document.getElementById("fishChart");
      const rect = canvas.getBoundingClientRect();
      const mouseX = event.clientX - rect.left;
      const mouseY = event.clientY - rect.top;

      const chartWidth = canvas.width;
      const margin = 60;
      const barWidth = (chartWidth - margin * 2) / this.fishData.length;

      let index = -1;

      // Detectamos si el cursor está sobre alguna barra
      this.fishData.forEach((fish, i) => {
        const x = margin + i * barWidth;
        const barHeight = (fish.population / Math.max(...this.fishData.map(f => f.population))) * (canvas.height - margin * 2);
        const y = canvas.height - margin - barHeight;

        // Verificamos si el mouse está sobre la barra
        if (mouseX >= x && mouseX <= x + barWidth - 10 && mouseY >= y && mouseY <= y + barHeight) {
          index = i;
        }
      });

      // Si el índice ha cambiado, actualizamos el estado de hover
      if (index !== this.hoveredIndex) {
        this.hoveredIndex = index;
        this.drawChart(); // Redibujamos el gráfico al cambiar el hover
      }
    }
  },

  mounted() {
    // Cargar los datos del JSON
    fetch("/fish_data.json")
      .then(response => response.json())
      .then(data => {
        this.fishData = data; // Almacena los datos
        this.drawChart(); // Dibuja el gráfico una vez que los datos están listos
      })
      .catch(error => console.error("Error al cargar el JSON:", error));

    // Añadir el evento para detectar el hover
    const canvas = document.getElementById("fishChart");
    canvas.addEventListener("mousemove", this.handleMouseMove);
  },

  beforeDestroy() {
    // Limpiar el evento al destruir el componente
    const canvas = document.getElementById("fishChart");
    canvas.removeEventListener("mousemove", this.handleMouseMove);
  }
};
  </script>
    

    <style scoped>
    .container-footer {
  background-color: #4392e0;
  text-align: center;
  overflow: hidden;
}

canvas {
 
  display: block;
  margin: 0 auto;
  border-radius: 8px;
  cursor: pointer;
}
    </style>