    <b-card-text>
      <p class="large-text">Els bancs de peixos són agrupacions de peixos que es desplacen junts per l'aigua, creant formes fascinants i dinàmiques. 
      <br>Aquesta conducta social es dona en moltes espècies, com els sardins, les tonyines i les mules. 
      <br>Els bancs de peixos poden tenir milions d'individus i es desplacen com una unitat, una estratègia que els ajuda a protegir-se 
