<div>
    <h3>LLEGEIX I CONTESTA LES PREGUNTES SOBRE EL TEXT</h3>
      <p>La pesca amb xarxa és una tècnica mil·lenària utilitzada per capturar peixos i altres organismes marins o d'aigua dolça. 
      <br>Consisteix en l'ús de xarxes de diferents tipus (com xarxes de trammell, xarxes de deriva o xarxes de embolic) que són llançades a l'aigua per capturar els peixos quan intenten passar a través d'elles. 
      <br> Aquest mètode pot ser realitzat tant en aigües costaneres com en profunditats més grans.
      <br>Les xarxes poden ser construïdes amb fils de diversos materials com el niló o el cotó, i les seves malles varien segons el tipus de peix que es vol capturar. 
      <br>Les xarxes de trammell, per exemple, s'utilitzen per atrapar espècies més petites, mentre que les xarxes d'embolcall es tanquen al voltant de grans bancs de peixos, permetent una captura massiva.
      <br>Tot i que la pesca amb xarxa és una de les tècniques més eficients per capturar grans quantitats de peixos, també pot tenir un impacte negatiu en el medi ambient. 
      <br>Això inclou la captura accidental d'espècies no desitjades (anomenada pesca incidental), la degradació dels fons marins i la destrucció d'ecosistemes fràgils com els coralls i les praderies marines. 
      <br>Per aquest motiu, en moltes regions es regulen les pràctiques de pesca amb xarxa per mitigar aquests efectes i garantir la sostenibilitat de les poblacions de peixos.
      <br>La pesca amb xarxa és una de les tècniques més antigues i utilitzades per a la captura de peixos i altres organismes aquàtics. Aquesta pràctica ha estat perfeccionada al llarg dels segles, 
      <br>adaptant-se a les necessitats i els canvis tecnològics, però el seu principi bàsic segueix sent el mateix: utilitzar una xarxa per atrapar els peixos a mesura que intenten escapar o moure’s a través de l’aigua.
      <br>Tipus de xarxes
      <br>Hi ha diversos tipus de xarxes utilitzades en la pesca, cadascuna dissenyada per a capturar diferents tipus de peixos o per operar en condicions específiques. 
      <br>Algunes de les més comunes són:
      <br>Xarxes de trammell: Aquestes xarxes tenen malles fixes que permeten que els peixos quedin atrapats quan intenten passar a través de les malles. 
      <br>Són utilitzades principalment per atrapar espècies més petites, com sardines, verats o altres peixos de mida mitjana.
      <br>Xarxes d’embolcall: Aquest tipus de xarxa es llança al voltant d’un banc de peixos i després es tanca com un sac, atrapant 
      <br>els peixos dins. Aquest mètode es fa servir per a capturar grans quantitats de peixos de manera més eficient, com tonyines,
      <br>bacallans o peixos d’aigües profundes.
      <br>Xarxes de deriva: Aquestes xarxes són llançades a l’aigua i deixades a deriva amb l’ajuda del corrent marí. Són utilitzades per 
      <br>capturar peixos que nedan a la superfície o prop de la zona costanera.
      <br>Xarxes de fondo: Aquestes xarxes s’utilitzen per a capturar peixos que viuen prop del fons marí. Són col·locades a la part inferior 
      <br>del mar i poden cobrir grans àrees per atrapar espècies com el lluç, la gamba i altres organismes bentònics.
      <br>Evolució de la pesca amb xarxa
      <br>La pesca amb xarxa ha evolucionat significativament des de la seva creació. Els primers humans utilitzaven xarxes fetes de materials 
      <br>naturals com el lli o el cotó. Amb el temps, els materials sintètics com el niló i el polièster van començar a utilitzar-se, augmentant 
      <br>la durabilitat i l'eficàcia de les xarxes.
      <br>A mesura que la tecnologia va avançant, també ho va fer la forma en què les xarxes es llançaven i s'utilitzaven. Avui dia, les xarxes 
      <br>modernes són controlades per sistemes electrònics que ajuden a detectar els bancs de peixos i garantir que es capturin les quantitats desitjades.
      <br>Impactes ambientals de la pesca amb xarxa
      <br>Tot i que la pesca amb xarxa és molt eficient, també té efectes negatius en el medi ambient. Alguns dels problemes més greus inclouen:
      <br>Pesca incidental: La captura no desitjada d'espècies que no són l'objectiu de la pesca (com tortugues, dofins i ocells marins) 
      <br>és un problema comú, especialment en xarxes de deriva i de fons.
      <br>Destrucció de hàbitats marins: Les xarxes poden danyar els ecosistemes marins fràgils, com les praderies de fanerògames marines 
      <br>i els esculls de corall, quan entren en contacte amb el fons marí o quan són arrossegades a través d'aquests hàbitats.
      <br>Despeses energètiques: Les operacions de pesca amb xarxa poden requerir molta energia, especialment quan es tracta de xarxes 
      <br>de gran mida o de pesca a profunditats molt grans, contribuint a l’emissió de gasos d’efecte hivernacle.
      <br>Regulació de la pesca amb xarxa
      <br>Per mitigar els impactes ambientals i garantir la sostenibilitat de les poblacions de peixos, molts països han establert
      <br>regulacions estrictes sobre la pesca amb xarxa. Aquestes regulacions inclouen:
      <br>Quotes de pesca: Establir límits sobre la quantitat de peixos que es poden capturar per evitar la sobreexplotació de les poblacions.
      <br>Tamaños mínims i màxims: Impedir la captura de peixos que són massa petits per reproduir-se o que estan en perill d'extinció.
      <br>Àrees protegides: Crear àrees marines protegides on la pesca amb xarxa estigui prohibida per conservar ecosistemes vulnerables.
      <br>En conclusió, la pesca amb xarxa continua sent una tècnica fonamental per a la indústria pesquera, però també planteja reptes importants per a la conservació marina. 
      <br>Amb la implementació de pràctiques més sostenibles i regulacions adequades, es poden mitigar molts dels 
      <br>seus efectes negatius, ajudant a mantenir un equilibri entre la necessitat humana de recursos marins i la protecció dels ecosistemes aquàtics.
    </p>
