<template>
    
    <div class="motriu">
        <h2>MOTRIU</h2>
        <h3>SEGUEIX ELS PASSOS PER PARTICIPAR EN EL SORTEIG</h3>
        <div>
          <b-img src="../assets/sorteig.jpg" fluid alt="sorteig kit de pesca"></b-img>
        </div>

       <div>
        <b-button variant="outline-light">CLICAR AQUÍ PER REGISTRAR-SE</b-button>
      </div>

      <div>
        <h4>2. Registra't amb el teu nom</h4>
          <b-form inline>
            <label class="mr-sm-2" for="inline-form-custom-select-pref">Nom</label>
            <b-form-select
              id="inline-form-custom-select-pref"
              class="mb-2 mr-sm-2 mb-sm-0"
              :options="[{ text: 'Escriu el nom', value: null }, 'Joan', 'Maria', 'Helena', 'Eduard',]"
              :value="null"
            ></b-form-select>
            <b-form-checkbox class="mb-2 mr-sm-2 mb-sm-0">no recordis el nom</b-form-checkbox>
            <b-button variant="primary">SUPRIMIR</b-button>
          </b-form>
        </div>

          <div>
            <h4>4. Clica en la teva edat</h4>
          <b-tabs content-class="mt-3" fill>
            <b-tab title="De 10 a 30 anys" active><p>No pots participar</p></b-tab>
            <b-tab title="31"></b-tab>
            <b-tab title="De 32 a 112 anys"><p>Hi ha un error en la edat</p></b-tab>
            <b-tab title="NO FUNCIONA" disabled><p>Qualsevol edat</p></b-tab>
          </b-tabs>
        </div>

        <div>
          <h4>3. Adjunta una imatge teva pescant</h4>
          <b-form-group label="AQUÍ" label-cols-sm="2" label-size="sm">
        <b-form-file id="file-small" size="sm"></b-form-file>
        </b-form-group>
        </div>


        <div>
          <h4>7. Envia la teva participació</h4>
          <b-button squared variant="success">NO ENVIAR</b-button>
          <b-button squared variant="outline-danger">ENVIAR</b-button>
        </div>

        <div>
          <h4>10. Confirma la participació, envia'n tantes com vulguis</h4>
          <div>
            <b-button @click="makeToast()">CONFIRMAR</b-button>
          </div>
        </div>
    
        <br><br>
        <footer aria-label="Footer">
  <div class="container-footer">
<p>PC-ando | &copy; Berta Güell i Sònia Soldevila | 2024-2025</p>
</div>
</footer>
  </div>
    
</template>
  
  <script>
  export default {
    data() {
      return {
        toastCount: 0
      }
    },
    methods: {
      makeToast(append = false) {
        this.toastCount++
        this.$bvToast.toast(`PARTICIPACIÓ. ${this.toastCount}`, {
          title: 'ENHORABONA, no s"ha enviat la teva participació',
          autoHideDelay: 5000,
          appendToast: append
        })
      }
    }
  }
  </script>
<style scoped>
.btn-secondary {
    color: #e09943;
    background-color: #4392e0;
    border-color: #4392e0;
}
.h4{
    margin-top: 0;
    margin-bottom: .5rem;
}
.container-footer {
  background-color: #4392e0;
  text-align: center;
  overflow: hidden;
}
</style>