import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import Ceguesa from '../views/Ceguesa.vue'
import Baixavisio from '../views/Baixavisio.vue'
import Auditiva from '../views/Auditiva.vue'
import Motriu from '../views/Motriu.vue'
import Cognitiva from '../views/Cognitiva.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/ceguesa',
    name: 'ceguesa',
    component: Ceguesa
  },
  {
    path: '/baixavisio',
    name: 'baixavisio',
    component: Baixavisio
  },
  {
    path: '/auditiva',
    name: 'auditiva',
    component: Auditiva
  },
  {
    path: '/motriu',
    name: 'motriu',
    component: Motriu
  },
  {
    path: '/cognitiva',
    name: 'cognitiva',
    component: Cognitiva
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  }
]

const router = new VueRouter({
  mode: "history",
  base: '/practica2discapacitats/', /*nombre de la carpeta donde se dirigirá github */
  routes
  })

export default router
